
.swing-counter-wrapper[data-v-f1e1ec08] {  
  /* Center element */
  position: fixed;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.swing-counter-wrapper[data-v-f1e1ec08]:hover {
  cursor: pointer;
}
.swing-counter[data-v-f1e1ec08] {
  color: green;
  font-size: 20em;
  line-height: 1.1em;
}

