
.controls[data-v-69b63ad8] {
  /* width: 100%; */
  text-align: center;
  /* Center element */
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.started[data-v-69b63ad8] {
  -webkit-transition: all .5s ease-in;
  transition: all .5s ease-in;
  /* transform: translate(-50%, 42vh); */
  position: absolute;
  top: unset;
  bottom: 10px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
button[data-v-69b63ad8] {
  width: 256px;
  height: 256px;
  border: none;
  outline: none;
  background: none;
  background-repeat: no-repeat;
  background-position: center center;
}
.started button[data-v-69b63ad8] {
  width: 50px;
  height: 50px;
}
button.play[data-v-69b63ad8] {
  background-image: url('../assets/icon-play-256x256.png');
}
.started button.play[data-v-69b63ad8] {
  background-image: url('../assets/icon-play-50x50.png');
}
button.pause[data-v-69b63ad8] {
  background-image: url('../assets/icon-pause-50x50.png');
}
.fade-enter-active[data-v-69b63ad8], .fade-leave-active[data-v-69b63ad8] {
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out;
}
.fade-enter[data-v-69b63ad8], .fade-leave-to[data-v-69b63ad8] {
  opacity: 0;
  -webkit-transform: scale(0.1);
          transform: scale(0.1);
}



